.MovieCard {
    position: relative;
    padding: 8px;
    color: #0c161e;
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    height: 100%;
    margin-bottom: 10px;
    -webkit-box-shadow: 5px 5px 25px 0 rgba(46,61,73,.2);
    box-shadow: 5px 5px 25px 0 rgba(46,61,73,.2);
    -webkit-transition: -webkit-box-shadow .3s ease-out;
    transition: -webkit-box-shadow .3s ease-out;
    -o-transition: box-shadow .3s ease-out;
    transition: box-shadow .3s ease-out;
    transition: box-shadow .3s ease-out, -webkit-box-shadow .3s ease-out;
    transition: box-shadow .3s ease-out,-webkit-box-shadow .3s ease-out;
}
.MovieList__item {
    width: 25%;
    padding: 20px;
}
.MovieCard__rating {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #e2b100;
    border: 1px solid #dea12f;
    width: 50px;
    height: 35px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    border-bottom-right-radius: 35%;
}
.MovieCard__poster {
    display: block;
    width: 100%;
    height: 180px;
    margin-bottom: 10px;
    -o-object-fit: cover;
    object-fit: cover;
}
.MovieInfo__title {
    line-height: 1.5;
    margin-bottom: 10px;
}
.MovieInfo__descr {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.3;
}
MovieInfo__release {
    margin-bottom: 16px;
    font-size: 16px;
}
.MovieCard__btn {
    position: absolute;
    display: block;
    outline: 0;
    border: 0;
    background-color: #e2b100;
    cursor: pointer;
    color: #0c161e;
    font-weight: 700;
    bottom: 8px;
    right: 8px;
    width: 35px;
    height: 35px;
    font-size: 24px;
    line-height: 35px;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(33,33,33,.5);
    box-shadow: 0 2px 2px 0 rgba(33,33,33,.5);
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
}
.MovieCard:hover {
    -webkit-box-shadow: 0 0 25px 2px rgba(230,236,241,.4);
    box-shadow: 0 0 25px 2px rgba(230,236,241,.4);
}
.MovieCard__btn:hover {
    -webkit-box-shadow: 0 4px 2px 0 rgba(33,33,33,.5);
    box-shadow: 0 4px 2px 0 rgba(33,33,33,.5);
    background-color: #3f51b5;
    color: #fff;
}


