.SearchForm {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
    padding: 8px;
    border-radius: 2px;
    background-color: #e2b100;
}
.SearchForm__input {
    padding: 8px;
    font-size: 18px;
    -ms-flex-positive: 1;
    flex-grow: 1;
    color: #212121;
    border: 0;
    border-radius: 6px;
}
.SearchForm__btn {
    display: none;
    position: absolute;
    border: 0;
    outline: 0;
    padding: 4px 8px;
    text-transform: uppercase;
    background-color: #3f51b5;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
}


